import React, { useState, useEffect } from "react";
import axios from "axios";
import {
  CCard,
  CCardBody,
  CCardGroup,
  CCardHeader,
  CAlert,
} from "@coreui/react";
import {
  CChartBar,
  CChartLine,
  CChartDoughnut,
  CChartPie,
  CChartPolarArea,
} from "@coreui/react-chartjs";
// import { DocsLink } from 'src/reusable'

const Charts = () => {
  const [item, SetItem] = useState([]);
  const [la, SetLa] = useState([]);
  const [spec, SetSpec] = useState([]);
  function getRandomColor() {
    let cols = ["#4CFA6C", "#FF79C6", "#BD85C8", "#F1FFDF", "#AF93F9"];
    const item = cols[Math.floor(Math.random() * cols.length)];
    console.log(item);
    return item;
  }

  const handleData = (e) => {
    console.log("handle", e);
    let ServicesConfig = {
      method: "get",
      url: `https://46.209.219.53:5004/home/chartLineSpec?date=${e}`,
    };
    axios(ServicesConfig)
      .then((Response) => {
        if (Response.status === 200) {
          console.log(Response);
          SetSpec(Response.data);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };

  useEffect(() => {
    let ServicesConfig = {
      method: "get",
      url: `https://46.209.219.53:5004/home/chartLine`,
    };
    axios(ServicesConfig)
      .then((Response) => {
        if (Response.status === 200) {
          console.log(Response.data);
          let ar = [];
          let la = [];
          Response.data.map((i) => {
            ar.push(i.cnt);
            la.push(i.insertDate);
          });
          SetItem(ar);
          SetLa(la);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  return (
    <CCardGroup columns className="cols-2">
      <CCard>
        <CCardHeader>
          Bar Chart
          {/* <DocsLink href="http://www.chartjs.org"/> */}
        </CCardHeader>
        <CCardBody>
          <CChartBar
            datasets={[
              {
                label: "",
                backgroundColor: "#f87979",
                data: item,
              },
            ]}
            labels={la}
            options={{
              tooltips: {
                enabled: true,
              },
              onClick: function (evt, element) {
                // console.log(element[0]);
                console.log(element[0]._index);
                handleData(
                  element[0]._chart.config.data.labels[element[0]._index]
                );
                // console.log(element[0]._chart.config.data.datasets[0].data)
                // console.log(element[0]._chart.config.data.labels)
                // console.log(element[0]._chart.config.data.labels)
                // var data = item.datasets[element[0]._chart_datasetIndex].data;
              },
            }}
            // onClick={(e) => handleData(e)}
          />
        </CCardBody>
      </CCard>
      <CCard>
        {spec.length ? (
          <>
            <CCardHeader></CCardHeader>
            <CCardBody style={{ backgroundColor: "#282A36" }}>
              <CAlert>
                <h4 className=""></h4>
                {spec.map((item) =>
                  Object.entries(item).map(([key, val]) => (
                    // console.log(key, val)
                    <p style={{ color: getRandomColor(), direction: "ltr" }} key={key}>
                      {key}: {val}
                    </p>
                  ))
                )}
                <hr />
              </CAlert>
            </CCardBody>
          </>
        ) : (
          ""
        )}
      </CCard>

      <CCard>
        <CCardHeader>Doughnut Chart</CCardHeader>
        <CCardBody>
          <CChartDoughnut
            datasets={[
              {
                backgroundColor: ["#41B883", "#E46651", "#00D8FF", "#DD1B16"],
                data: [40, 20, 80, 10],
              },
            ]}
            labels={["VueJs", "EmberJs", "ReactJs", "AngularJs"]}
            options={{
              tooltips: {
                enabled: true,
              },
            }}
          />
        </CCardBody>
      </CCard>

    </CCardGroup>
  );
};

export default Charts;